.article {
    padding: 16px;
    color: var(--typography-body);
}

.article ul li::marker {
    font-size: 10px; /* Set the desired font size */
  }

.article .headings-display-big {
    font-size: 24px;
    font-family: "DM Sans";
    font-weight: 400;
    font-style: normal;
    line-height: 30px;
    text-decoration: none;
    text-transform: none;
}

.visual-link {
    margin-bottom: 40px;
}


.link-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(318px, 1fr));
    grid-auto-rows: repeat(auto);
    grid-gap: 24px;
    margin-bottom: 40px;
}

.card-container {
    display: grid;
    grid-template-columns: repeat(1fr);
    grid-auto-rows: repeat(auto);
    grid-gap: 24px;
    margin-bottom: 40px;
}

.table-container {
    --border-radius: 10px;
    --border-color: #000;
    /* border: 1px solid var(--border-color); */
    border-radius: var(--border-radius);
    overflow: hidden;
    overflow-x: auto;
    white-space: nowrap;
}

table {
    border-collapse: collapse;
    width: 100%;
    border-radius: 10px;
    /* border: 1px solid var(--border-color); */
    display: block;
}

th,
td {
    padding: 16px;
    text-align: left;
    border-bottom: 1px solid var(--border-color);
    border-right: 1px solid var(--border-color);
    /* Add border to the right of each cell */
}

tr:last-child td {
    border-bottom: none;
    /* Remove border from the last row */
}

td:last-child {
    border-right: none;
    /* Remove border from the last cell in each row */
}

.custom-scrollbar::-webkit-scrollbar {
    height: 5px;
    background-color: #D9D9D9;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
    background-color: var(--color-primary-500);
    border-radius: 3px;
}

.custom-scrollbar {
    border: 1px solid var(--border-color);
    /* height: 500px; */
}

