@media only screen and (min-width: 600px) {
    .article {
        padding: 32px;
    }
}

@media only screen and (min-width: 1240px) {
    .article {
        padding: 167px 32px;
    }

    .headings-display-big {
        font-size: 36px;
        font-family: "DM Sans";
        font-weight: 400;
        font-style: normal;
        line-height: 40px;
        text-decoration: none;
        text-transform: none;
    }
}